
.avatar-img {
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 15px;
  line-height: 30px;
  text-align: center;
  background-color: #808080;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  color: #fff;
}
#app {
  margin: 20px auto;
  width: 800px;
  height: 600px;

  overflow: hidden;
  border-radius: 3px;
}
#app .sidebar, #app .main {
  height: 100%;
}
#app .sidebar {
  float: left;
  width: 200px;
  color: #f4f4f4;
  background-color: #2e3238;
}
#app .main {
  position: relative;
  overflow: hidden;
  background-color: #eee;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
body, html {
  height: 100%;
  overflow: hidden;
}
body, ul {
  margin: 0;
  padding: 0;
}
body {
  color: #4d4d4d;
  font: 14px/1.4em 'Helvetica Neue', Helvetica, 'Microsoft Yahei', Arial, sans-serif;
  background-size: cover;
  font-smoothing: antialiased;
}
ul {
  list-style: none;
}




